.landingScreen{
  background-repeat: no-repeat;
  background-size: contain;
}
.landingScreenImg{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.landingScreenImg img{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -10;
  background-repeat: no-repeat;
  overflow: hidden;
}

.landingOverlay{
  width: 100%;
  background: rgb(0, 0, 0);
  opacity: .6;
  position: absolute;
  /* z-index: ; */
}

.landingTitleWrapper {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landingTitleWrapper h4{
  color: #DBDBDB;
  font-weight: light;
  margin: 0px;
  padding: 0px;
}

.typewriter {
  color: #DBDBDB;
  width: 37ch;
  -webkit-animation: typing 8s steps(22) infinite,
    blink 0.5s step-end infinite alternate;
          animation: typing 8s steps(22) infinite,
    blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-size: 2em;
}

.events{
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  /* position: absolute; */
  -webkit-transform: translateY(-150px);
          transform: translateY(-150px);
}

.eventCard{
  width: 240px;
  height: 205px;
  overflow: hidden;
  position: relative;
  -webkit-filter: drop-shadow(5px 5px 10px rgba(60, 247, 251, 0.2));
          filter: drop-shadow(5px 5px 10px rgba(60, 247, 251, 0.2));
  border-radius: 4px;
}

.eventCard h4{
  font-family: coolvetica;
  letter-spacing: 0.2ch;
}

.eventCard:hover{
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}



.eventImg{
  width: 100%;
